// /components/utils/cart.ts

interface Flower {
  nameEN: string;
  nameKA: string;
  price: string;
  img: string;
  quantity?: number;
}


export const getCart = (): Flower[] => {
  if (typeof window === "undefined") return [];
  return JSON.parse(localStorage.getItem("cart") || "[]");
};

export const saveCart = (cart: Flower[]) => {
  if (typeof window === "undefined") return;
  localStorage.setItem("cart", JSON.stringify(cart));
};

export const addToCart = (flower: Flower) => {
  const cart = getCart();
  const existing = cart.find(item => item.nameEN === flower.nameEN);
  if (existing) {
    existing.quantity = (existing.quantity ?? 0) + 1;

  } else {
    cart.push({ ...flower, quantity: 1 });
  }
  saveCart(cart);
};


export const removeFromCart = (nameEN: string) => {
  let cart = getCart();
  cart = cart.filter((item) => item.nameEN !== nameEN);
  saveCart(cart);
};

export const clearCart = () => {
  if (typeof window === "undefined") return;
  localStorage.removeItem("cart");
};
