"use client";

import { useEffect, useState } from "react";
import {
  Box,
  Typography,
  Card,
  CardMedia,
  CardContent,
  Button,
} from "@mui/material";
import { getCart, removeFromCart, clearCart } from "../components/utils/cart";

interface Flower {
  nameEN: string;
  nameKA: string;
  price: string;
  img: string;
}

const CartPage = () => {
  const [cart, setCart] = useState<Flower[]>([]);

  useEffect(() => {
    setCart(getCart());
  }, []);

  const handleRemove = (name: string) => {
    removeFromCart(name);
    setCart(getCart());
  };

  const handleClear = () => {
    clearCart();
    setCart([]);
  };

  return (
    <Box
      sx={{
        mt: 15,
        px: { xs: 2, sm: 5 },
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
      }}
    >
      <Typography
        variant="h4"
        fontWeight="bold"
        mb={3}
        textAlign="center"
        sx={{ fontSize: { xs: 24, sm: 32 } }}
      >
        Your Cart:
      </Typography>

      {cart.length === 0 ? (
        <Typography color="gray" textAlign="center">
          Cart is empty
        </Typography>
      ) : (
        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            gap: 3,
            width: "100%",
            maxWidth: 800,
            mb: 5,
          }}
        >
          {cart.map((item, index) => (
            <Card
              key={index}
              sx={{
                display: "flex",
                flexDirection: { xs: "column", sm: "row" },
                alignItems: "center",
                p: 2,
                gap: 2,
              }}
            >
              <CardMedia
                component="img"
                image={item.img}
                alt={item.nameEN}
                sx={{
                  width: { xs: "100%", sm: 150 },
                  height: 150,
                  objectFit: "cover",
                  borderRadius: 2,
                }}
              />
              <CardContent
                sx={{
                  flex: 1,
                  width: "100%",
                  textAlign: { xs: "center", sm: "left" },
                }}
              >
                <Typography variant="h6" fontWeight="bold">
                  {item.nameEN}
                </Typography>
                <Typography color="crimson" fontWeight="bold">
                  {item.price}
                </Typography>
              </CardContent>
              <Button
                onClick={() => handleRemove(item.nameEN)}
                color="error"
                variant="outlined"
                sx={{
                  mt: { xs: 1, sm: 0 },
                  alignSelf: { xs: "center", sm: "auto" },
                }}
              >
                Remove
              </Button>
            </Card>
          ))}

          <Button
            variant="contained"
            color="error"
            onClick={handleClear}
            sx={{
              alignSelf: { xs: "center", sm: "flex-start" },
              mt: 2,
            }}
          >
            Clear Cart
          </Button>
        </Box>
      )}
    </Box>
  );
};

export default CartPage;
